<template>
  <div class="header">
        <router-link to="/">
            <img src="/suning/static/img/img1.png" alt="">
        </router-link>
        <h3>苏宁生鲜</h3>
        <div @click="none"  class="header-right">
            <img src="/suning/static/img/img2.png" alt="">
            <ul v-if="show">
                <li>首页</li>
                <li>购物车</li>
                <li>搜索</li>
                <li>全部分类</li>
                <li>我的易购</li>
            </ul>
            <p v-if="show"></p>
        </div>
    </div>
</template>

<script>
export default {
  data(){
    return{
      show:false
    }
    
  },
  methods:{
    none:function(){
      this.show=!this.show;
    }
  },
  // mouted(){
  //     none()
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header {
  background: #fff;
  height: .44rem;
  padding: 0 .14rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header a img {
    width: .2rem;
    height: .25rem; }
  .header h3 {
    font-size: .24rem;
    color: #11ac3f;
    font-weight: 100; }

.header-right img {
  width: .25rem;
  height: .25rem; }

.header-right ul {
    position: absolute;
    right: 0;
    top:40px;
    font-size: .14rem;
    z-index: 99999;
    background: #fff;
}
.header-right ul li{
    background: #484F55;
    width: 1.25rem;
    height:.44rem;
    margin-bottom: 1px;
    text-align: center;
    line-height: .44rem;
    color:#fff;
}
.header-right p{
    position: absolute;
    right: 10px;
    top: 20px;
    width: 0;
    height: 0;
    border:10px solid transparent;
    border-bottom-color:#484F55; 
    /* display: none; */
}
</style>


