<template>
 <div class="qiang">
    <div class="time">
        <div class="time-title">今日必抢</div>
        <div class="count-dowm">
            <span class="info">距结束</span>
            <span class="node">{{h}}</span>
            <span class="txt">:</span>
            <span class="node">{{m}}</span>
            <span class="txt">:</span>
            <span class="node">{{s}}</span>
        </div>
        <div class="more">更多></div>
    </div>
    <suning-index-test/>
 </div>
</template>

<script>
import test from "./test"
export default {
    data(){
        return{
            h:"",
            m:"",
            s:""
        }
    },
 mounted(){
     this.countTime();
 },
  components:{
      "suning-index-test":test
  },
  methods:{
      countTime: function () {
                //获取当前时间
                var date = new Date();
                var now = date.getTime();
                //设置截止时间
                var endDate = new Date("2019-4-19 23:23:23");
                var end = endDate.getTime();
                //时间差
                var leftTime = end - now;
                //定义变量 d,h,m,s保存倒计时的时间
                if (leftTime >= 0) {
                    // d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
                    this.h = Math.floor(leftTime / 1000 / 60 / 60 % 24);
                    this.m = Math.floor(leftTime / 1000 / 60 % 60);
                    this.s = Math.floor(leftTime / 1000 % 60);
                }
                // console.log(this.s);
                //递归每秒调用countTime方法，显示动态时间效果
                setTimeout(this.countTime, 1000);
            }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


