<template>
  <div class="banner">
        <div class="banne-bg"></div>
        <div class="bann-mc"></div>
        <div class="swipe">
            <div class="swiper-container swpe">
                <div class="swiper-wrapper">
                    <div class="swiper-slide"><img src="/suning/static/img/swiper1.jpg" alt=""></div>
                    <div class="swiper-slide"><img src="/suning/static/img/swiper1.jpg" alt=""></div>
                    <div class="swiper-slide"><img src="/suning/static/img/swiper1.jpg" alt=""></div>
                </div>
            </div>
            <!-- <img src="../../assets/img/swiper1.jpg" alt=""> -->
        </div>
    </div>
</template>

<script>
import Swiper from 'swiper';
export default {
  mounted(){
  let banner=new Swiper ('.swpe', {
         autoplay:true,
         autoplay: {
            delay: 2000,
            stopOnLastSlide: false,
            disableOnInteraction: true,
            },
    loop: true,
  })        
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.swiper-container{
    width: 100%;
}
.swiper-container img{
    width: 100%;
    height:1.49rem;
}
</style>


