<template>
    <div class="header">
        <a href="##">
            <img src="/suning/static/img/img1.png" alt="">
        </a>
        <h3>助农扶贫</h3>
        <div @click="none" class="header-right">
            <img src="/suning/static/img/img2.png" alt="">
            <ul v-if="show">
                <li>首页</li>
                <li>购物车</li>
                <li>搜索</li>
                <li>全部分类</li>
                <li>我的易购</li>
            </ul>
            <p v-if="show"></p>
        </div>
    </div>
</template>

<script>
export default {
    data(){
    return{
      show:false
    }
    
  },
  methods:{
    none:function(){
      this.show=!this.show;
    }
  },
}
</script>

<style scoped>

</style>



