<template>
<div>
 <div class="section-right">
            <div class="section-right-title">
                <img src="/suning/static/img/titleleft.png" alt="">
                <span>精选推荐</span>
                <img src="/suning/static/img/titleright.png" alt="">
            </div>
            <div class="products">
                <ul>
                    <li v-for="good in data.goods">
                        <a href="##">
                            <img :src="good.pic" alt="">
                            <p>{{good.title}}</p>  
                        </a>
                        
                    </li>
                    
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props:["data"]
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .section .section-right {
    width: 2.88rem;
    padding: .1rem;
    box-sizing: border-box; }
    .section .section-right .section-right-title {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2.69rem;
      margin: .15rem 0 .2rem; }
      .section .section-right .section-right-title img {
        width: .665rem;
        height: .085rem; }
      .section .section-right .section-right-title span {
        font-size: .14rem;
        font-weight: 600; }
    .section .section-right .products ul {
      display: flex;
      flex-wrap: wrap; }
      .section .section-right .products ul li {
        width: .89rem; }
        .section .section-right .products ul li img {
          width: .56rem;
          height: .56rem;
          margin: 0 auto; }
        .section .section-right .products ul li p {
          font-size: .12rem;
          color: #cacaca;
          text-align: center; }
</style>


