<template>
  <div>
      <sunimg-common-fHeader/>
      <sunimg-fenlei-search/>
      <sunimg-fenlei-section v-if="fenleiInfo" :data="fenleiInfo"/>
  </div>
</template>

<script>
import fHeader from "../components/common/fHeader"
import fenlei from "../components/fenlei/search"
import section from "../components/fenlei/section"
import axios  from  "axios"

export default {
      data(){
           return {
               fenleiInfo:{}
           }
        },
     components:{
            "sunimg-common-fHeader":fHeader,
            "sunimg-fenlei-search":fenlei,
            "sunimg-fenlei-section":section
        },
    methods:{
       getfenleiInfo() {
        axios.get('../../static/mocks/fenlei.json')
          .then((res)=>{
              console.log(res.data);
              this.fenleiInfo=res.data;
          })
      }
       
    },

 mounted(){
        this.getfenleiInfo()
      }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
  background: #fff!important;
}
</style>


