import Vue from 'vue'
import Router from 'vue-router'
import fenlei from '../pages/fenlei'
import index from '../pages/index'
import qianggou from '../pages/qianggou'
import fupin from '../pages/fupin'
import detail from '../pages/detail'
import sectionRight from "../components/fenlei/sectionRight"
import sectionRight1 from "../components/fenlei/sectionRight1"
import elem from "../components/elem"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/fenlei',
      name: 'fenlei',
      component: fenlei,
      children:[
        {
          path: '/sectionRight',
          name: 'sectionRight',
          component: sectionRight
        },
        {
          path: '/sectionRight1',
          name: 'sectionRight1',
          component: sectionRight1
        },
      ]
    },
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/qianggou',
      name: 'qianggou',
      component: qianggou
    },
    {
      path: '/fupin',
      name: 'fupin',
      component: fupin
    },
    {
      path: '/detail',
      name: 'detail',
      component: detail
    },
    {
      path: '/elem',
      name: 'elem',
      component: elem
    }
  ]
})



